import React,{useState} from 'react';
import PropTypes from 'prop-types';
import { Panel, List, Button, Group, Div, Avatar, PanelHeader,
  		Epic, Tabbar, TabbarItem, View} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon28GraphOutline from '@vkontakte/icons/dist/28/graph_outline';
import Icon28WalletOutline from '@vkontakte/icons/dist/28/wallet_outline';
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';
import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import Icon28UserCircleOutline from '@vkontakte/icons/dist/28/user_circle_outline';

//components
import Quests from "./MainPages/Quests";
import Settings from "./MainPages/Settings";
const Home = (props) => {
	let [activeStory, setActiveStory] = useState('quest');
	let [popout, setPopout] = useState(null);
	let onStoryChange = (e) => {
		setActiveStory(e.currentTarget.dataset.story);
	}
	return 	<Panel id={props.id} style={{ marginTop: -55}}>
		<Epic activeStory={activeStory} tabbar={
			<Tabbar>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'quest'}
					data-story="quest"
					text="Квесты"
				><Icon28ArticleOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'leaderBoard'}
					data-story="leaderBoard"
					text="Лидеры"
				><Icon28GraphOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'payment'}
					data-story="payment"
					text="Магазин"
				><Icon28MarketOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'settings'}
					data-story="settings"
					text="Профиль"
				><Icon28UserCircleOutline/></TabbarItem>
			</Tabbar>
		}>
			<Quests id='quest'/>
			{/* <View id="quest" activePanel="quest"> */}
				<Quests id='quest'/>
			{/* </View> */}
			<View id="leaderBoard" activePanel="leaderBoard">
				<Panel id="leaderBoard">
					<PanelHeader>Поиск</PanelHeader>
				</Panel>
			</View>
			<View id="payment" activePanel="payment">
				<Panel id="payment">
					<PanelHeader>Уведомления</PanelHeader>
				</Panel>
			</View>
			<View id="settings" activePanel="settings">
				<Settings id={'settings'}/>
			</View>
		</Epic>
	</Panel>
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
