import React from 'react';
import PropTypes from 'prop-types';
import persik from '../img/persik.png';
import './Start.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import {Panel, PanelHeader, PanelHeaderButton, platform, IOS,
        Div, Button, Group, CardGrid, Card, Header, Gallery} from '@vkontakte/vkui';
const osname = platform();

const Start = props => (
    <Panel id={props.id} style={{ marginTop: -70}}>
        {/*<PanelHeader>*/}
        {/*    VK-APP*/}
        {/*</PanelHeader>*/}
        <Group separator='hide'>
            <Gallery
                slideWidth="100%"
                style={{ height: '100vh' }}
                bullets="dark"
                align="center"

            >
                <div className='pres_block' style={{ background: "#db4e44", color: 'white'}}>
                    <img src="https://image.flaticon.com/icons/svg/2274/2274846.svg" alt=""/>
                    Выполняй ежедневные задания
                </div>
                <div className='pres_block' style={{ background: "#44a1db", color: 'white'}}>
                    <img src="https://image.flaticon.com/icons/svg/2736/2736289.svg" alt=""/>
                    Поднимайся в таблице лидеров
                </div>
                <div className='pres_block' style={{ background: '#44db88', color: 'white'}}>
                    <img src="https://image.flaticon.com/icons/svg/706/706164.svg" alt=""/>
                    Начни вести здоровый образ жизни
                </div>
                <div className='pres_block' style={{ background: '#fff', fontSize: 30}}>
                    Стань лучше
                    <Div style={{ display: 'flex', justifyContent: 'center',  }}>
                        <Button size='xl' stretched style={{}} onClick={props.go} data-to='reg'>Попробовать</Button>
                    </Div>
                </div >

            </Gallery>
        </Group>
        <Group>
        </Group>
    </Panel>
);

Start.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        }),
    }),
};

export default Start;
