import React from 'react';
import {Group, CardGrid, Card,
    ModalPage, ModalPageHeader, ModalRoot} from '@vkontakte/vkui';

let Task = ({popoutType, handleClick}) => {

    return (
        <CardGrid onClick={() => handleClick(popoutType)}>
            <Card size='1' style={{width: 'calc(100% - 20px)', borderRadius: '5px', padding: '10px'}}>
                <div style={{height: 45}}>
                    Привет
                </div>
            </Card>
        </CardGrid>
    );
}


export default Task;


