import React, {useState} from 'react';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import PropTypes from 'prop-types';
import styles from './registration.module.css';
import { FormLayout, FormLayoutGroup, Input } from '@vkontakte/vkui';
import {Panel, PanelHeader, PanelHeaderButton, platform, IOS, PanelHeaderSimple,
    Avatar, Slider, Radio, Button, Div, CardGrid, Card, Checkbox} from '@vkontakte/vkui';
const osname = platform();
const MAX_WEIGHT_USER = 250;
const MAX_HEIGHT_USER = 250;

let Registration = (props) => {
    let [userWeight, setUserWeight] = useState('');
    let [userHeight, setUserHeight] = useState('');
    return (
        <Panel id={props.id}>
            <PanelHeader
                left={<PanelHeaderButton onClick={props.go} data-to="start">
                    {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                </PanelHeaderButton>}
            >
                Регистрация
            </PanelHeader>

            <FormLayout style = {{}}>
                <FormLayoutGroup top="Укажите Ваш вес(кг)" style = {{ }}>
                    <Input type="number" placeholder="Ваш Вес" value={userWeight} onChange={event => setUserWeight(Math.min(Number(event.target.value), MAX_WEIGHT_USER) == 0 ? "" : String(Math.min(Number(event.target.value), MAX_WEIGHT_USER)))}/>
                </FormLayoutGroup>
                <FormLayoutGroup top='Укажите Ваш рост(см)'>
                    <Input type="number" placeholder="Ваш рост" value={userHeight} onChange={event => setUserHeight(Math.min(Number(event.target.value), MAX_HEIGHT_USER) == 0 ? "" : String(Math.min(Number(event.target.value), MAX_HEIGHT_USER)))}/>
                </FormLayoutGroup>
                <FormLayoutGroup top='Укажите Ваш тип телосложения' bottom={'При выборе опирайтесь на Ваш индекс массы тела'}>
                    <div>
                        <Radio name="radio" value="1" description="мало жира, тощие мускулы" defaultChecked>Худое</Radio>
                        <Radio name="radio" value="2" description="Средний баланс между жиром и мускулами">Обычное</Radio>
                        <Radio name="radio" value="3" description="Мускулатура, отстутствие жира">Спортивное</Radio>
                        <Radio name="radio" value="4" description="Тело круглое и мягкое, высокий процент жировой ткани">Полное</Radio>
                        {/*<Radio name="radio" value="4">Выше среднего</Radio>*/}
                        {/*<Radio name="radio" value="5">Сильное</Radio>*/}
                    </div>
                </FormLayoutGroup>
                <FormLayoutGroup>
                    <Checkbox>Я согласен на обработку персональных данных</Checkbox>
                </FormLayoutGroup>
                <Div>
                    <Button size='xl' stretched style={{}} onClick={props.go} data-to='home'>Зарегистрироваться</Button>
                </Div>
            </FormLayout>
        </Panel>
    )
};
Registration.propTypes = {
    go: PropTypes.func.isRequired,
    id: PropTypes.string.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        })
    })
};
export default Registration;